import React, { useEffect } from 'react';
import LogicFlow from '@logicflow/core';
import { Menu } from '@logicflow/extension';
import ExampleHeader from '../../../../components/example-header';
import '@logicflow/extension/lib/style/index.css';

const config = {
  stopScrollGraph: true,
  stopZoomGraph: true,
  textEdit: false,
};

const data = {
  nodes: [
    {
      id: 10,
      type: 'rect',
      x: 150,
      y: 80,
      text: ''
    },
    {
      id: 20,
      type: 'rect',
      x: 400,
      y: 80,
      text: ''
    }
  ],
  edges: [
    {
      type: 'line',
      sourceNodeId: 10,
      targetNodeId: 20,
      text: '',
      endPoint: {
        id: '150-60',
        x: 350,
        y: 80
      }
    }
  ]
};
interface MenuLogicFlow extends LogicFlow {
  addMenuConfig(config: any): void
}
let lf: MenuLogicFlow;
export default function CustomMenuExample() {
  useEffect(() => {
    LogicFlow.use(Menu);
    lf = new LogicFlow({
      ...config,
      grid: {
        size: 10,
        type: 'dot'
      },
      container: document.querySelector('#graph') as HTMLElement
    }) as MenuLogicFlow;
    lf.extension.menu.addMenuConfig({
      nodeMenu: [
        {
          text: '',
          callback() {
            alert('');
          }
        },
        {
          text: '',
          callback(node: any) {
            alert(`
              id${node.id}
              ${node.type}
              (x: ${node.x}, y: ${node.y})`
            );
          },
        }
      ],
      edgeMenu: [
        {
          text: '',
          callback(edge: any) {
            
            alert(`
              id${edge.id}
              ${edge.type}
              id${edge.sourceNodeId}
              id${edge.targetNodeId}`
            );
          },
        }
      ],
      graphMenu: [
        {
          text: '',
          callback() {
            alert('');
          }
        }
      ]
    });
    lf.render(data);
  }, []);

  return (
    <>
      <ExampleHeader content="" />
      <div id="graph" className="viewport" />
    </>
  )
}
