import React, { useEffect, useState } from 'react';
import LogicFlow, { EdgeType } from '@logicflow/core';
import { CurvedEdge } from '@logicflow/extension';
import ExampleHeader from '../../../components/example-header';

type EdgeTypeUn = EdgeType | 'curved-edge';

const config = {
  stopScrollGraph: true,
  stopZoomGraph: true,
};

const data = {
  nodes: [
    {
      id: 10,
      type: 'rect',
      x: 150,
      y: 100,
      text: ''
    },
    {
      id: 20,
      type: 'circle',
      x: 400,
      y: 100,
      text: ''
    },
    {
      id: 30,
      type: 'diamond',
      x: 300,
      y: 300,
      text: ''
    },
    {
      id: 33,
      type: 'diamond',
      x: 600,
      y: 250,
      text: '2'
    }
  ],
  edges: [
    {
      type: 'polyline',
      sourceNodeId: 10,
      targetNodeId: 20,
      text: '',
      startPoint: {
        id: '150-60',
        x: 150,
        y: 60
      }
    },
    {
      type: 'line',
      sourceNodeId: 10,
      targetNodeId: 20,
      text: '',
      endPoint: {
        id: '150-60',
        x: 350,
        y: 100
      }
    },
    {
      type: 'bezier',
      sourceNodeId: 10,
      targetNodeId: 30,
      text: '',
      endPoint: {
        id: '150-60',
        x: 300,
        y: 250
      }
    },
    {
      type: 'curved-edge',
      sourceNodeId: 30,
      targetNodeId: 33,
      text: '',
    }
  ]
};

export default function EdgeExample() {
  const [type, setType] = useState('');
  const [lf, setLf] = useState<LogicFlow>();

  useEffect(() => {
    // @ts-ignore
    LogicFlow.use(CurvedEdge);
    const logicflow = new LogicFlow({
      ...config,
      container: document.querySelector('#graph') as HTMLElement
    });
    logicflow.render(data);
    setLf(logicflow);
  }, []);
  const setEdgeType = (type: EdgeTypeUn, typeName: string): void => {
    const logicflow = lf as LogicFlow;
    logicflow.setDefaultEdgeType(type as EdgeType);
    setType(typeName);
  }

  return (
    <>
      <ExampleHeader
        content=""
        githubPath="/basic/edge/index.tsx"
      />
      <div>{type}</div>
      <div>
        <button onClick={() => setEdgeType('line', '')}></button>
        <button onClick={() => setEdgeType('polyline', '')}></button>
        <button onClick={() => setEdgeType('curved-edge', '')}></button>
        <button onClick={() => setEdgeType('bezier', '')}></button>
      </div>
      <div id="graph" className="viewport" />
    </>
  )
}
